document.addEventListener('DOMContentLoaded', function() {
    crearGaleria();
});

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes'); // Me situo en la clase .galeria-imagenes para ir agregando las imagenes

    console.log(galeria);

    for( let i = 1; i <= 12; i++ ) {
        const imagen = document.createElement('IMG'); // Creo un elemento tipo imagen
        imagen.src = `build/img/thumb/${i}.webp`; // Pongo la direccion donde encontrarla
        imagen.dataset.imagenId = i;

        // AÃ±ado la funcion de mostrarImagen
        imagen.onclick = mostrarImagen;

        const lista = document.createElement('LI'); // Creo la lista
        lista.appendChild(imagen); // Inserto en la lista la imagen 'i'

        galeria.appendChild(lista); // Inserto en la galeria la lista con la imagen
    }
}

function mostrarImagen(evento) {
    const id = parseInt(evento.target.dataset.imagenId); // El id pasa de string a entero.

    // Generar la  imagen
    const imagen = document.createElement('IMG'); // Creo elemento  tipo imagen
    imagen.src = `build/img/grande/${id}.webp`; // Le asigno lugar a guardar

    const overlay = document.createElement('DIV'); // Creo una division. overlay = aumenta la imagen.
    overlay.appendChild(imagen); // Inserto la imagen en esa division
    overlay.classList.add('overlay');

    // Cuando se da click, cerrar  la imagen
    overlay.onclick = function() {
        overlay.remove();
        body.classList.remove('fijar-body');
    }

    //Boton para cerrar la imagen
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = 'X';
    cerrarImagen.classList.add('btn-cerrar');

    // Cuando se presiona, se cierra la imagen

    cerrarImagen.onclick = function() {
        overlay.remove();
        body.classList.remove('fijar-body');
    }

    overlay.appendChild(cerrarImagen);

    //Mostrar en el HTML
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');
}